.nav{
    width: 80%;
    height: 60px;
    margin: 0 auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    z-index: 1;
    background-color: #EBE0E9;
}

.head{
    width: 300px;
}

.item{
    width: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}

a {
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    color: black;
    transition: all 0.4s ease;
}

a:hover {
     color: lightcoral;
     transition: all 0.4s ease;
 }

.item:hover{
    height: 56px;
    width: 150px;
    border-bottom: 3px solid #1a4394;
    background: rgba(0,0,0,.2)
}

.active {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 56px;
    width: 150px;
    border-bottom: 3px solid #1a4394;
    background: rgba(0,0,0,.2)
}